<template>
  <ul class="menu-toc-list" :class="className" v-if="toc.length">
    <li v-for="(item, index) in toc" :key="index" :class="{ 'active-toc-menu': activeText === item.text }">
      <router-link :to="{ path: $route.path, hash: `#${item.text}` }" @click.native.stop="handleHashClick(item)">
        {{ `${index + 1}. ${item.text}` }}
      </router-link>
    </li>
  </ul>
</template>

<script>
import { emitter, generateId } from '@/utils'

export default {
  props: {
    path: String,
    className: String,
  },

  data() {
    return {
      toc: [],
      activeText: '',
    }
  },

  created() {
    emitter.$on('content-ready', this.getTocList)
    emitter.$on('change-active-text', this.changeActiveText)
  },

  methods: {
    changeActiveText(text) {
      if(this.activeText === text) {
        return
      }
      this.activeText = text
    },

    handleHashClick(hash) {
      this.activeText = hash.text
      emitter.$emit('hash-menu-click')
      this.$scrollTo(`#${generateId(hash.text)}`)
    },

    getTocList() {
      const matches = document.querySelectorAll('#page-content-wrap h2')
      this.toc = Array.from(matches).map(item => ({
        text: item.innerText
      }))
      if(this.$route.hash) {
        const hash = decodeURIComponent(this.$route.hash)
        this.activeText = hash.slice(1)
        const id = generateId(this.activeText)
        emitter.$emit('hash-menu-click')
        this.$scrollTo(`#${id}`)
      }
      const container = document.getElementById('scroll-container')
      container && (container.scrollTo({
        top: 0,
        behavior: "smooth",
      }))
    }
  },

  beforeDestroy() {
    emitter.$off('content-ready', this.getTocList)
    emitter.$off('change-active-text', this.getTocList)
  },
}
</script>

<style scoped>
.menu-toc-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.menu-toc-list li {
  padding: 5px 0 5px 44px;
}

.submenu-toc-list li {
  padding-left: 60px;
  font-size: 12px;
}

.menu-toc-list a {
  color: #262626;
}

.submenu-toc-list a {
  color: #555555;
}

.menu-toc-list a, .menu-toc-list a:hover {
  text-decoration: none;
}

.menu-toc-list a:hover, .active-toc-menu a {
  color: #2a54ff;
}
</style>


