<template>
  <el-container style="height: calc(100% - 60px); padding-top: 32px;">
    <el-aside width="230px" class="aside-section">
      <ul class="aside-menus">
        <li v-for="(menu, index) in menus"
          @click="handleMenuClick(menu, index)"
          :id="menu.path"
          :key="index"
        >
          <span :class="['menu-item', { 'menu-item-active': activePath === menu.path}]">
            {{ menu.name }}
          </span>
          <TOC :path="activePath" v-if="activePath === menu.path"  :key="activePath"/>
          <ul class="aside-menus aside-sub-menus" v-if="Array.isArray(menu.children) && openMenuIndex === index">
            <li v-for="(submenu, j) in menu.children"
              @click.stop="handleSubMenuClick(submenu)"
              :id="submenu.path"
              :key="j"
            >
              <span class="menu-item  submenu-item" :class="[{ 'menu-item-active': activePath === submenu.path }]">
                {{ submenu.name }}
              </span>
              <TOC :path="activePath" v-if="activePath === submenu.path"  :key="activePath" className="submenu-toc-list"/>
            </li>
          </ul>
        </li>
      </ul>
    </el-aside>
    <el-main id="scroll-container" style="position: relative;">
      <router-view :lang="currentLang" />
      <div class="contact-service" @click="handleServiceClick">
        <svg
          id="Layer_1"
          x="0"
          y="0"
          viewBox="0 0 15 16"
          xml:space="preserve"
          aria-hidden="true"
        >
          <path
            d="M1.3,16c-0.7,0-1.1-0.3-1.2-0.8c-0.3-0.8,0.5-1.3,0.8-1.5c0.6-0.4,0.9-0.7,1-1c0-0.2-0.1-0.4-0.3-0.7c0,0,0-0.1-0.1-0.1 C0.5,10.6,0,9,0,7.4C0,3.3,3.4,0,7.5,0C11.6,0,15,3.3,15,7.4s-3.4,7.4-7.5,7.4c-0.5,0-1-0.1-1.5-0.2C3.4,15.9,1.5,16,1.5,16 C1.4,16,1.4,16,1.3,16z M3.3,10.9c0.5,0.7,0.7,1.5,0.6,2.2c0,0.1-0.1,0.3-0.1,0.4c0.5-0.2,1-0.4,1.6-0.7c0.2-0.1,0.4-0.2,0.6-0.1 c0,0,0.1,0,0.1,0c0.4,0.1,0.9,0.2,1.4,0.2c3,0,5.5-2.4,5.5-5.4S10.5,2,7.5,2C4.5,2,2,4.4,2,7.4c0,1.2,0.4,2.4,1.2,3.3 C3.2,10.8,3.3,10.8,3.3,10.9z"
          />
        </svg>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import { mapGetters } from 'vuex'
import debounce from 'lodash.debounce'

import TOC from './TOC.vue'

import { getRootConfigByLang } from '@/config/index'
import { getAppConfig, emitter, LANG_KEY } from '@/utils'

export default {
  components: {
    TOC
  },
  data() {
    return {
      moduleConfig: null,
      show: false,
      activePath: '',
      openMenuIndex: -1, // 打开的一级菜单
      debounceScroll: () => {},
    }
  },

  computed: {
    ...mapGetters(['currentLang']),
    rootConfig() {
      return getRootConfigByLang(this.currentLang)
    },
    menus() {
      if (this.moduleConfig) {
        const appConfig = getAppConfig()
        const res = []
        this.moduleConfig.forEach(item => {
          const root = item.path
          if(Array.isArray(item.children)) {
            const menu = {
              path: root,
              name: appConfig[root].name,
              children: [],
            }
            item.children.forEach(child => {
              menu.children.push({
                path: `${root}/${child.path}`,
                name: appConfig[`${root}/${child.path}`].name,
              })
            })
            res.push(menu)
          } else {
            res.push({
              path: root,
              name: appConfig[root].name,
            })
          }
        })
        return res
      }
      return []
    },
  },

  watch: {
    '$route.meta.module': {
      handler(val) {
        this.moduleConfig = this.rootConfig[val]
        this.activePath = `/${val}/changelog`
      },
      immediate: true
    },
    rootConfig: {
      handler(val) {
        this.moduleConfig = val[this.$route.meta.module]
      },
      deep: true,
    },
    '$route.path'() {
      this.init()
    },
  },

  methods: {
    redirectByPath(path) {
      this.activePath = path
      this.$router.push(path)
    },

    handleMenuClick(menu, index) {
      if(this.activePath === menu.path) {
        return
      }

      if(Array.isArray(menu.children)) {
        this.openMenuIndex = index
        return 
      }
      this.openMenuIndex = -1
      this.redirectByPath(menu.path)
    },

    handleSubMenuClick(menu) {
      if(this.activePath === menu.path) {
        return
      }
      this.redirectByPath(menu.path)
    },

    init() {
      this.activePath = this.$route.path
      let openMenuIndex = -1
      for(let i = 0; i < this.menus.length; i++) {
        const menu = this.menus[i]
        if(this.activePath === menu.path) {
          break
        }
        if(Array.isArray(menu.children)) {
          for(let j = 0; j < menu.children.length; j++) {
            if(this.activePath === menu.children[j].path) {
              openMenuIndex = i
              break
            }
          }
        }
        if (openMenuIndex !== -1) {
          break
        }
      }
      this.openMenuIndex = openMenuIndex
    },

    getAnchors() {
      this.anchors = Array.from(document.querySelectorAll('#page-content-wrap h2'))
    },

    scroll() {
      if(this.hashMenuClick) {
        // 点击左侧的hash菜单会导致滚动
        this.hashMenuClick = false
        return
      }
      const scrollTop = this.container.scrollTop
      if(this.anchors) {
        const index = this.anchors.findIndex(dom => dom.offsetTop >= scrollTop)
        const activeIndex = Math.max(0, index - 1)
        const anchor = this.anchors[activeIndex]
        if(anchor) {
          const text = anchor.dataset.text
          emitter.$emit('change-active-text', text)
          const path = location.hash.slice(1).replace(/(#.*)/, '')
          location.hash = `#${path}#${encodeURIComponent(text)}`
          document.title = `${text} | BingX API Docs`
        }
      }
    },

    handleServiceClick() {
      let url = 'https://bingx.com/zh-tw/feedback/?referPath=api'
      if(this.currentLang === LANG_KEY.en) {
        url = 'https://bingx.com/en-us/feedback/?referPath=api'
      }
      window.open(url, '_blank')
    }
  },

  created() {
    this.init()
    this.debounceScroll = debounce(this.scroll, 200)
    emitter.$on('content-ready', this.getAnchors)
    emitter.$on('hash-menu-click', () => {
      this.hashMenuClick = true
    })
  },

  mounted() {
    this.container = document.getElementById('scroll-container')
    if(this.container) {
      this.container.addEventListener('scroll', this.debounceScroll)
    }
  },

  beforeDestroy() {
    if(this.container) {
      this.container.removeEventListener('scroll', this.debounceScroll)
    }
    emitter.$off('content-ready')
    emitter.$off('hash-menu-click')
  }
}
</script>

<style scoped>
.aside-section {
  border-right: 1px solid #F0F0F0;
  height: calc(100% - 32px);
}

.aside-menus {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.menu-item {
  font-family: 'HarmonyOS Sans SC';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  display: inline-block;
  width: 100%;
  color: #262626;
  padding: 10px 0 10px 32px;
  background: #FFFFFF;
  border-left: 4px solid transparent;
  cursor: pointer;
}

.submenu-item {
  padding-left: 44px;
}

.menu-item-active, .menu-item:hover {
  color: rgb(42, 84, 255);
  background-color: rgba(42, 84, 255, 0.08);
}

.menu-item-active {
  border-left-color: #2A54FF;
}


.aside-menu >>> .el-menu-item.is-active {
  border-left: 4px solid rgb(78, 112, 255);
}

.contact-service {
  position: fixed;
  z-index: 100;
  right: 16px;
  bottom: 10px;
  font-size: 14px;
  background: #2a54ff;
  color: #fff;
  overflow: hidden;
  width: 53px;
  height: 53px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  padding: 0 15px;
  box-sizing: border-box;
  border-radius: 100px;
  cursor: pointer;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.12);
  /* transition: all 0.2s ease-out; */
}

.contact-service svg {
  display: block;
  color: #fff;
  fill: #fff;
  height: 24px;
  width: 24px;
  min-height: 24px;
  min-width: 24px;
}
</style>


